@mixin nav_active {
  position: relative;
  &::after {
    opacity: 0;
    content: '';
    position: absolute;
    bottom: -0.4em;
    left: 0.4em;
    right: 0.4em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

  &.on {
    font-weight: bold;
    &::after {
      opacity: 1;
    }
  }
}

@mixin ellipse($line: 2) {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin select-nav($width: 80px) {
  .nav {
    flex: none;
    border-radius: 15px;
    display: flex;
    border: 1px solid #BBBBBB;
    margin: 10px auto;
    width: min-content;

    &_link {
      position: relative;
      flex: none;
      width: $width;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;

      &:hover {
        background-color: #F5F5F5;
      }

      &.on {
        background-color: #BBBBBB;
        color: #fff;
      }

      .lt_ico {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(20%, -50%);
        border: 1px solid #fff;
        border-radius: 1000px;
        background-color: var(--themeColor);
        line-height: 1;
        padding: 2px 4px;
        color: #fff;
      }
    }
  }
}

.domUserEdit {
  &_main {
    display: flex;
    padding: 20px 30px;

    .left {
      width: 490px;
      .row {
        display: flex;
        margin-bottom: 20px;
        .key {
          width: 50px;
          line-height: 30px;
        }

        .value {
          flex: auto;
          .input {
            display: block;
            width: 100%;
            height: 30px;
            padding: 0 10px;
            font-size: 12px;
            border-radius: 4px;
            border: 1px solid #E5E5E5;
          }
          .signature {
            border: 1px solid #E5E5E5;
            &Length {
              text-align: right;
              padding: 4px;
            }
          }
          .textarea {
            display: block;
            outline: none;
            width: 100%;
            height: 50px;
            padding: 4px 10px;
            font-size: 12px;
            border-radius: 4px;
            font-family: inherit;
            resize: none;
            border: none;
          }

          .gender {
            display: inline-flex;
            align-items: center;
            line-height: 30px;
            margin-right: 2em;

            .ico {
              position: relative;
              margin-right: 0.5em;
              width: 18px;
              height: 18px;
              border-radius: 50%;
              border: 1px solid #D0D0D0;

              &::before {
                opacity: 0;
                width: 12px;
                height: 12px;
                content: '';
                border-radius: 50%;
                background-color: var(--themeColor);
              }
            }

            .radio:checked {
              & + .ico {
                border-color: var(--themeColor);
                &::before {
                  opacity: 1;
                }
              }
            }
          }

          .radio {
            display: none;
          }

          .select {
            padding: 0 6px;
            height: 30px;
            width: 135px;
            border: 1px solid #E5E5E5;
            border-radius: 2px;
          }

          .actions {
            padding-top: 20px;
            font-size: 14px;
            .disabled {
              cursor: initial;
              opacity: 0.6;
            }
          }
        }
      }
    }
  }
}
